.about-control {
    background-color: #f5eed2;
    padding-top: 60px;
    padding-bottom: 0px;
    font-weight: 900;
    margin-bottom: 0px;
}

.image-control img {
    width: 100%;
    margin-top: 100px;
}

.about-description {
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.about-description header {
    font-size: 50px;
    line-height: 50px;
    color: #88CDDB;
    font-style: italic;
    font-weight: 900;
    margin-bottom: 10px;
}

.about-description p {
    color: #40210a;
}

.about-description p span {
    color: #000000;
}

.about-description header span {
    color: #000000;
}


@media (min-width: 767px) {
    .about-control {
        background-color: #f5eed2;
        padding-top: 60px;
        padding-bottom: 130px;
        font-weight: 900;
    }

    .image-control img {
        width: 100%;
        margin-top: 0px;
    }

}