.header-control {
    /* background-image: url('../../assets/DickBackground.png'); */
    background-color: #172A55;
    /* this can be changed to header-back or dickbackground */
    background-color: #f5eed2;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    position: relative;
    padding-top: 20px;
    padding-bottom: 20px;
    height: 40px;
    background-repeat: repeat;
    background-size: cover;
    background-position: 50% 100%;
}

.header-control .navbar {
    width: 60%;
    background-color: #ffffff;
    margin-top: 0px;
    padding-top: 0px;
    border-radius: 20px;
    /* border: solid #E8B331 10px; */
    /* outline: 5px solid black; */
    /* box-shadow: 0 0 0 3px rgb(0, 0, 0); */
    border: solid 1px #E8B331;
}

.navbar-nav {
    display: flex;
    align-items: center;
}

.navbar-nav .nav-link {
    transition-duration: .4s;
    color: white !important;
    font-size: 25px;
    font-weight: 700;
    font-family: newerfont;
    background: -webkit-linear-gradient(#E8B331, #E8B331);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    transition-duration: .4s;
}

@font-face {
    font-family: newerfont;
    src: url(/src/StrangerThings-Regular.ttf);
}

.navbar-nav .nav-link:hover {
    transition-duration: .4s;
    color: #88CDDB !important;
    background: -webkit-linear-gradient(white, white);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    transition-duration: .4s;
}

.header-content {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
}

@font-face {
    font-family: newfont;
    src: url(/src/ReporterRegular.ttf);
}

.header-content h1 {
    font-size: 51px;
    font-family: newfont;
    color: black;
    /* font-style: italic; */
    margin-bottom: 30px;
    margin-top: 30px;
    font-weight: 900;
    text-align: center;
    /* background-color: #6D3612; */
    /* padding: 25px; */
    /* border: solid 10px black;
    border-radius: 20px; */
}

.header-nav {
    position: absolute;
    top: 20px;
    max-width: 1200px;
    width: 1200px;
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.nav-item {
    margin-right: 20px;
    color: white;
    font-size: 13px;
    font-weight: 700;
    cursor: pointer;
}

.nav-item :hover {
    color: blue;
}

.thePicture {
    height: 0px;
    border: solid #632006 0px;
    border-radius: 0px;
    padding: 0px;
    /* background-color: #FF9859; */
}

@media (min-width: 767px) {
    .header-control {
        /* background-image: url('../../assets/OPENSEA_Banner.png'); */
        background-color: #f5eed2;
        /* this can be changed to header-back or dickbackground */
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: space-between;
        position: relative;
        padding-top: 120px;
        /* padding-bottom: 20px; */
        height: 500px;
        background-repeat: none;
        background-position: 0% -20%;
    }

    .thePicture {
        height: 314.66px;
        width: 1259.33px;
        /* border: solid #E8B331 10px; */
        border-radius: 20px;
        /* background-color: #a14912; */
        background-color: #f5eed2;
    }

    .header-control .navbar {
        width: 70%;
        background-color: #000000;
        margin-top: 0px;
        padding-top: 0px;
        border-radius: 20px;
        border: none;
        /* border: solid #E8B331 10px; */
        /* outline: 5px solid black; */
        /* box-shadow: 0 0 0 3px rgb(0, 0, 0); */
    }
}